import { useContext, createContext, useEffect, useState } from "react";
import React from "react";

const ClientContext = createContext();

export function useClient() {
  return useContext(ClientContext);
}

export function ClientProvider(props) {
  const [clientList, setClientlist] = useState([]);
  const [contactList, setContactList] = useState([]);
  const [activeClient, setActiveClient] = useState({});
  const [formData, setFormData] = useState({});

  const selectClient = (patientid) => {
    fetch(`http://www.ivronlogs.icu:8080/rs/api/v1/patient/${patientid}`).then(
      (response) =>
        response.json().then((data) => {
          console.log(data);
          setActiveClient(data);
        })
    );
  };
  const getContactList = (patientid) => {
    fetch(`http://www.ivronlogs.icu:8080/rs/api/v1/contact`).then(
      (response) =>
        response.json().then((data) => {
          console.log(data);
          setContactList(data);
        })
    );
  };
  const getClientContact = (patientid) => {
    if(contactList){
      const  clientContact = contactList.filter((contact, index) => contact.patientid == patientid)
      console.log(clientContact)
      return clientContact
    }
    return null
  }
  const getFormFields = () => {
    fetch(`http://www.ivronlogs.icu:8080/rs/api/v1/grouplist`).then(
      (response) =>
        response.json().then(async (data) => {
          console.log(data);
          let groupObject = {
            gender: [],
            ethnicity: [],
            phoneType: [],
            contactType: [],
          };
          await data.forEach((group, index) => {
            if (group.grouplistid >= 1 && group.grouplistid < 3) {
              groupObject.gender = [...groupObject.gender, group.groupvalue];
            }
            if (group.grouplistid >= 3 && group.grouplistid < 13) {
              groupObject.ethnicity = [
                ...groupObject.ethnicity,
                group.groupvalue,
              ];
            }
            if (group.grouplistid >= 18 && group.grouplistid < 21) {
              groupObject.phoneType = [
                ...groupObject.phoneType,
                group.groupvalue,
              ];
            }
            if (group.grouplistid >= 21 && group.grouplistid < 25) {
              groupObject.contactType = [
                ...groupObject.contactType,
                group.groupvalue,
              ];
            }
          });
          setFormData(groupObject);
        })
    );
  };

  const getClientList = () => {
    fetch("http://www.ivronlogs.icu:8080/rs/api/v1/patient").then((response) =>
      response.json().then((data) => {
        let clientArray = [];
        data.forEach((client) =>
          clientArray.push({
            name: client.lastname + ", " + client.firstname,
            patientid: client.patientid,
          })
        );
        console.log(clientArray);
        setClientlist(clientArray);
      })
    );
  };
  useEffect(() => {
    if (!clientList.length > 0) getClientList();
    if (!formData.length > 0) getFormFields();
    if (!contactList.length > 0) getContactList();
  }, []);

  return (
    <ClientContext.Provider value={{ clientList, contactList, getClientContact, selectClient, activeClient, formData}}>
      {props.children}
    </ClientContext.Provider>
  );
}
